import styled from 'styled-components'
import { useDispatch, useSelector } from 'react-redux'
import { logout } from '../../utils/reducers/loginState'

import { Wrapper } from '../Main'

import Logo from '../../components/Logo'
import MaterialIcon from '../../components/MaterialIcon'
import Promise from '../../components/Promise'
import SmallButtonItem from '../../components/SmallButtonItem'
import Rabbit from './LoginMain/Rabbit'
import CustomContainer from '../../components/CustomContainer'
import Container from '../../components/Container'
import { useNavigate } from 'react-router-dom'
import React from 'react'
import axios from 'axios'
import { ResponseError } from '../../utils/error'
import MoneyInfo from './LoginMain/MoneyInfo'
import { WISH_INIT_STATE } from '../../utils/constant'

function LoginMain() {
  const dispatch = useDispatch()

  const { token, uuid } = useSelector(state => state.loginState)

  const [money, setMoney] = React.useState(0)
  const [wish, setWish] = React.useState(WISH_INIT_STATE)
  const [custom, setCustom] = React.useState("2;1;0")

  const fetch = React.useCallback(
    async (token, uuid) => {
      try {
        const res = await axios.get(`/api/rabbit/mypage/${uuid}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })

        switch (res.status) {
          case 200:
            setMoney(res.data.result.money)
            setWish(res.data.result.wish)
            setCustom(res.data.result.custom)
            break
          default:
            throw new ResponseError(' .', res)
        }
      } catch (err) {
        const res = err.ResponseError

        switch (res.status) {
          case 401:
            alert(' .  .')
            dispatch(logout())
            window.location.reload()
            break
          case 404:
            alert(`${res.data.result.message}`)
            dispatch(logout())
            window.location.reload()
            break
          default:
            alert('   .    .')
            navigate('/')
        }
      }
    },
    [setMoney, dispatch]
  )

  React.useEffect(() => {
    fetch(token, uuid)
  }, [])

  const navigate = useNavigate()
  return (
    <Container>
      <Logo sx={2.5} />
      <Wrapper>
        <ButtonWrapper>
          <SmallButtonItem
            background="--white"
            color="--pink"
            onClick={() => navigate(`/letter/${uuid}`)}
          >
            <MaterialIcon iconName="link" color="--pink" />  
          </SmallButtonItem>

          <SmallButtonItem onClick={() => navigate(`/custom`)}>
              
          </SmallButtonItem>

          <SmallButtonItem
            background="--pink-50"
            color="--pink-500"
            onClick={() => dispatch(logout())}
          >
            
          </SmallButtonItem>
        </ButtonWrapper>

        <Promise defaultText={wish} />

        <MoneyInfo value={money} />
      </Wrapper>

      <CustomContainer
        money={money}
        debug={false}
        color={custom.split(';')[2]}
        accessory={custom.split(';')[3]}
        isCustom={false}
      />

      <Label>  2 3</Label>
    </Container>
  )
}

const ButtonWrapper = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
`

const Label = styled.div`
  width: 100%;

  font-family: nanumRound;
  font-weight: bold;
  font-size: max(1.2rem, 21px);

  text-align: center;

  background: white;
  color: var(--brown);
  padding: max(1rem, 18px);
`

export default LoginMain
