import PropTypes from 'prop-types'
import styled from 'styled-components'
import FlowerText from '../../components/FlowerText'

function MoneyInfo({ money = 0 }) {
  return (
    <FlowerText>
      <Content>
         <span className="bold">{money.toLocaleString()}</span>  !
      </Content>
    </FlowerText>
  )
}

MoneyInfo.propTypes = {
  money: PropTypes.number,
}

const Content = styled.div`
  display: flex;
  align-items: center;
  gap: 12px;

  font-family: nanumRound;
  font-weight: 600;
  font-size: 16px;

  .bold {
    font-weight: 800;
    color: var(--pink);
    font-size: 28px;
  }

  white-space: nowrap;
`

export default MoneyInfo
