import React, { useCallback, useEffect, useState } from 'react'

import { useNavigate } from 'react-router-dom'

import ButtonItem from '../components/ButtonItem'
import { SubTitle, Wrapper } from './Main'

import { BottomText, Input } from './Login'
import { useDispatch } from 'react-redux'
import { login } from '../utils/reducers/loginState'

import axios from 'axios'
import Logo from '../components/Logo'
import LinkItem from '../components/LinkItem'
import Container from '../components/Container'
import { ResponseError } from '../utils/error'

function CreateAccount() {
  const navigate = useNavigate()
  const [userID, setUserID] = useState('')
  const [nickname, setNickname] = useState('')
  const [password, setPassword] = useState('')
  const [passwordRepeat, setPasswordRepeat] = useState('')

  const dispatch = useDispatch()

  const handleIdChange = e => {
    setUserID(e.target.value)
  }
  const handleNicknameChange = e => {
    setNickname(e.target.value)
  }
  const handlePwChange = e => {
    setPassword(e.target.value)
  }

  const handlePwReChange = e => {
    setPasswordRepeat(e.target.value)
  }

  const attemptJoin = React.useCallback(
    async (userID, password, nickName) => {
      try {
        const res = await axios.post('/api/users/join', {
          userID: userID,
          password: password,
          nickName: nickName,
        })

        const code = res.status
        if (code === 200) {
          alert(' ')
          dispatch(login(res.data.result.jwt, res.data.result.uuid))
          navigate('/')
        } else {
          throw new ResponseError(' .', res)
        }
      } catch (err) {
        const res = err.response

        if (res.status === 409) {
          alert(` : ${res.data.result.message}`)
          window.location.reload()
        } else {
          alert('   .    .')
          navigate('/')
        }
      }
    },
    [dispatch, navigate]
  )

  const handleSubmit = useCallback(
    e => {
      if (!(4 <= userID.length)) {
        alert(' 4   .')
      } else if (!(2 <= nickname.length)) {
        alert(' 2   .')
      } else if (!(4 <= password.length && password.length <= 20)) {
        alert(' 4  20  .')
      } else if (password !== passwordRepeat) {
        alert('   .')
      } else {
        attemptJoin(userID, password, nickname)
      }
    },
    [userID, password, passwordRepeat, nickname, attemptJoin]
  )

  const onCheckEnter = e => {
    if (e.key === 'Enter') {
      handleSubmit()
    }
  }
  return (
    <Container>
      <Wrapper gap={3}>
        <Logo />
        <Wrapper gap={1.2}>
          <SubTitle>!</SubTitle>
          <Input
            type="text"
            name="userID"
            placeholder=""
            autocomplete="off"
            onChange={handleIdChange}
            onKeyDown={onCheckEnter}
          />
          <Input
            type="text"
            name="nickname"
            placeholder=""
            autocomplete="off"
            onChange={handleNicknameChange}
            onKeyDown={onCheckEnter}
          />
          <Input
            type="password"
            name="password"
            placeholder=""
            autocomplete="off"
            onChange={handlePwChange}
            onKeyDown={onCheckEnter}
          />
          <Input
            type="password"
            name="password"
            placeholder=" "
            autocomplete="off"
            onChange={handlePwReChange}
            onKeyDown={onCheckEnter}
          />
        </Wrapper>
        <Wrapper>
          <ButtonItem onClick={handleSubmit}></ButtonItem>
          <LinkItem target="/login">
            <BottomText> ? </BottomText>
          </LinkItem>
        </Wrapper>
      </Wrapper>
    </Container>
  )
}

export default CreateAccount
