import styled from 'styled-components'
import React, { useCallback, useState } from 'react'

import { useNavigate } from 'react-router-dom'
import { useDispatch } from 'react-redux'

import LinkItem from '../components/LinkItem'
import ButtonItem from '../components/ButtonItem'
import { SubTitle, Wrapper } from './Main'
import { login } from '../utils/reducers/loginState'

import axios from 'axios'
import Logo from '../components/Logo'
import Container from '../components/Container'
import { ResponseError } from '../utils/error'

function Login() {
  const navigate = useNavigate()
  const dispatch = useDispatch()

  //  
  const [userID, setUserID] = useState('')
  const [password, setPassword] = useState('')

  const handleIdChange = e => {
    setUserID(e.target.value)
  }
  const handlePwChange = e => {
    setPassword(e.target.value)
  }

  const attemptLogin = async (userID, password) => {
    try {
      const res = await axios.post('/api/users/login', {
        userID: userID,
        password: password,
      })

      switch (res.status) {
        case 200:
          alert(' .')
          dispatch(login(res.data.result.jwt, res.data.result.uuid))
          navigate('/')
          break
        default:
          throw new ResponseError(' .', res)
      }
    } catch (err) {
      const res = err.response
      switch (res.status) {
        case 401:
        case 404:
          alert(` : ${res.data.result.message}`)
          window.location.reload()
          break
        default:
          alert('   .    .')
          navigate('/')
      }
    }
  }

  const handleSubmit = useCallback(
    e => {
      if (!(4 <= userID.length)) {
        alert(' 4   .')
      } else if (password.length === 0) {
        alert('  .')
      } else {
        attemptLogin(userID, password)
      }
    },
    [userID, password]
  )

  const onCheckEnter = e => {
    if (e.key === 'Enter') {
      handleSubmit()
    }
  }

  return (
    <Container>
      <Wrapper gap={4}>
        <Logo />
        <Wrapper gap={1.2}>
          <SubTitle>!</SubTitle>
          <Input
            type="text"
            name="userID"
            placeholder=""
            onChange={handleIdChange}
            onKeyDown={onCheckEnter}
          />
          <Input
            type="password"
            name="password"
            placeholder=""
            onChange={handlePwChange}
            onKeyDown={onCheckEnter}
          />
        </Wrapper>

        <Wrapper>
          <ButtonItem onClick={handleSubmit}></ButtonItem>

          <LinkItem target="/sign-up">
            <BottomText>?  </BottomText>
          </LinkItem>
        </Wrapper>
      </Wrapper>
    </Container>
  )
}

export const Input = styled.input`
  width: max(16rem, 280px);
  padding: max(1rem, 18px);
  color: var(--brown);

  font-family: nanumRound;
  font-weight: bold;
  font-size: max(1rem, 16px);
  text-align: center;

  border: none;
  border-radius: max(0.5rem, 9px);
  filter: drop-shadow(3px 3px 2px var(--light-300));

  :focus {
    outline: 2px solid var(--pink-200);
  }

  ::placeholder {
    text-align: center;
    color: var(--brown-100);
  }
`

export const BottomText = styled.div`
  font-family: nanumRound;
  font-weight: 600;
  font-size: max(1rem, 16px);
`

export default Login
