const ITEMS_PER_PAGE = 9

function LetterBox() {
  const { uuid, token } = useSelector(state => state.loginState)

  const [letterData, setLetterData] = React.useState([])

  const [page, setPage] = React.useState(0)
  const [isEndPage, setIsEnd] = React.useState(true)

  const navigate = useNavigate()
  const dispatch = useDispatch()

  const fetch = useCallback(async () => {
    try {
      const res = await axios.get(
        `/api/letter/${uuid}/getLetter?page=${page}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      switch (res.status) {
        case 200:
          setLetterData(letterData.concat(res.data.result.content))
          setIsEnd(res.data.result.last)
          break
        default:
          throw new ResponseError(' .', res)
      }
    } catch (err) {
      const res = err.response

      switch (res.status) {
        case 401:
          alert(' .  .')
          dispatch(logout())
          navigate('/login')
          break
        default:
          alert('   .    .')
      }
    }
  }, [uuid, token, page])

  React.useEffect(() => {
    fetch()
  }, [page])

  return (
    <Container>
      <Wrapper gap={2}>
        <Logo sx={2.5} />

        <MoneyInfo money={125000} />

        <LetterWrapper>
          {letterData.map((item, idx) => (
            <Letter
              key={idx}
              id={item.id}
              money={item.money}
              author={item.author}
              content={item.content}
            />
          ))}
        </LetterWrapper>
        {isEndPage ? null : (
          <SmallButtonItem
            background="--white"
            color="--pink"
            onClick={() => {
              setPage(prev => prev + 1)
            }}
          >
            
          </SmallButtonItem>
        )}
      </Wrapper>
      <ButtonItem onClick={() => navigate('/')}></ButtonItem>
    </Container>
  )
}

const LetterWrapper = styled.div`
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  gap: 36px;
`
export default LetterBox
